.TodoListItem {
  font-size: 1.25rem;
}

.TodoListItem button {
  width: 35px;
  margin: 3px;
}

.TodoListItemLabel {
  margin-left: 1.25rem;
  line-height: 35px;
  cursor: pointer;
  user-select: none;
}

.TodoListItem.done .TodoListItemLabel {
  text-decoration: line-through;
}

.TodoListItem.important .TodoListItemLabel {
  font-weight: bold;
  color: steelblue;
}
.save {
  color: green !important;
  font-weight: bold;
  position: relative;
  right: 35%;
}
